@import url('https://fonts.googleapis.com/css2?family=Mulish:ital,wght@1,200&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Mulish', sans-serif;
}
.navbar{
  background-color: #fff !important;
}
.navbar-brand{
  font-size: 1.9rem !important;
  color: blueviolet !important;
}
.navbar a{
  font-weight: bold;
  text-transform: capitalize;
}
.menu_active{
  font-weight: bold;
  border-bottom: 1px solid aquamarine;
}
.navbar a:hover{
  color: aquamarine !important;
}
#header .brand-name{
  color: blue;
}
@media(max-width:991px){
  .navbar-brand{
    font-size: 1.5rem i !important;
  }
  .menu_active{
    font-weight: bold;
    border-bottom: none;
  }
}
#header{
  width: 100%;
  height: 70vh;
  margin-top: 70px;
}
#header h1{
  margin: 0 0 10px o;
  font-size: 48px;
  font-weight: 700;
  color: black;
}
#header h2{
  color: rgb(21, 17, 26);
  font-size: 24px;
}
#header img{
  text-align: right;
  height: 50vh;;
  /* margin-left: 36%; */
}
#header .animated{
  animation: up-down 2s ease ease-in-out infinite alternate-reverse both;
}
.card-img-top{
  height: 30vh;
}